import React from "react";
import "../styles/abt.css";
import Layout from "../components/Layout";

function AboutUs() {
  return (
    <>
      <Layout className="bodySearch">
        <div className="description">
          <p>
            <b>
              <br></br>
              Welcome to our team! Learn more about the talented individuals who
              make things happen.
            </b>
          </p>
          <br></br>
        </div>

        <div className="members">
          <div className="member">
            <img src="Dheeraj_n.jpg" alt="Member 1" />
            <h2>Dheeraj Verma</h2>
          </div>

          <div className="member">
            <img src="new.jpg" alt="Member 2" />
            <h2>Nilesh Yadav</h2>
          </div>

          <div className="member">
            <img src="akash.jpg" alt="Member 3" />
            <h2>Akash Sharma</h2>
          </div>

          <div className="member">
            <img src="devansh.jpg" alt="Member 4" />
            <h2>Devansh Jain</h2>
          </div>
        </div>
      </Layout>
    </>
  );
}

export default AboutUs;
