import React from "react";
import Layout from "../components/Layout";
import { Link } from "react-router-dom";
import { Button } from "antd";

function Corn() {
  return (
    <Layout>
      <p>Image Size Should be (256x256)</p>
      <iframe
        title="corn"
        className="frame"
        src="https://224883397097be5ba7.gradio.live/"
      ></iframe>
      <Link to="/plant">
        <Button className="btn">Back</Button>
      </Link>
    </Layout>
  );
}

export default Corn;
