import { BrowserRouter, Routes, Route } from "react-router-dom";
import HomePage from "./pages/HomePage";
import Login from "./pages/Login";
import Register from "./pages/Register";
import { useSelector } from "react-redux";
import Spinner from "./components/Spinner";
import ProtectedRoute from "./components/ProtectedRoute";
import PublicRoute from "./components/PublicRoute";
import Search from "./pages/Search";
import NotificationPage from "./pages/NotificationPage";
import AboutUs from "./pages/AboutUs";
import PlantList from "./pages/PlantList";
import Apple from "./PlantList/Apple";
import Grape from "./PlantList/Grape";
import Corn from "./PlantList/Corn";
import Cherry from "./PlantList/Cherry";
import Potato from "./PlantList/Potato";
import Peach from "./PlantList/Peach";
import Tomato from "./PlantList/Tomato";
import Strawberry from "./PlantList/strawberry";
import Pepper from "./PlantList/Pepper";
function App() {
  const { loading } = useSelector((state) => state.alerts);
  return (
    <>
      <BrowserRouter>
        {loading ? (
          <Spinner />
        ) : (
          <Routes>
            <Route
              path="/search"
              element={
                <ProtectedRoute>
                  <Search />
                </ProtectedRoute>
              }
            />
            <Route
              path="/aboutus"
              element={
                <ProtectedRoute>
                  <AboutUs />
                </ProtectedRoute>
              }
            />
            <Route
              path="/plant"
              element={
                <ProtectedRoute>
                  <PlantList />
                </ProtectedRoute>
              }
            />
            <Route
              path="/corn"
              element={
                <ProtectedRoute>
                  <Corn />
                </ProtectedRoute>
              }
            />
            <Route
              path="/grape"
              element={
                <ProtectedRoute>
                  <Grape />
                </ProtectedRoute>
              }
            />
            <Route
              path="/cherry"
              element={
                <ProtectedRoute>
                  <Cherry />
                </ProtectedRoute>
              }
            />
            <Route
              path="/potato"
              element={
                <ProtectedRoute>
                  <Potato />
                </ProtectedRoute>
              }
            />

            <Route
              path="/apple"
              element={
                <ProtectedRoute>
                  <Apple />
                </ProtectedRoute>
              }
            />
            <Route
              path="/strawberry"
              element={
                <ProtectedRoute>
                  <Strawberry />
                </ProtectedRoute>
              }
            />
            <Route
              path="/tomato"
              element={
                <ProtectedRoute>
                  <Tomato />
                </ProtectedRoute>
              }
            />
            <Route
              path="/peach"
              element={
                <ProtectedRoute>
                  <Peach />
                </ProtectedRoute>
              }
            />
            <Route
              path="/pepper"
              element={
                <ProtectedRoute>
                  <Pepper />
                </ProtectedRoute>
              }
            />
            <Route
              path="/notification"
              element={
                <ProtectedRoute>
                  <NotificationPage />
                </ProtectedRoute>
              }
            />
            <Route
              path="/login"
              element={
                <PublicRoute>
                  <Login />
                </PublicRoute>
              }
            />
            <Route
              path="/register"
              element={
                <PublicRoute>
                  <Register />
                </PublicRoute>
              }
            />
            <Route
              path="/"
              element={
                <ProtectedRoute>
                  <HomePage />
                </ProtectedRoute>
              }
            />
          </Routes>
        )}
      </BrowserRouter>
    </>
  );
}

export default App;
